import React, { useState, useEffect } from 'react'
import './LocalData.css'
import Chart from "react-apexcharts"

function LocalData() {
    
    const [dataVirginia, setDataVirginia] = useState("");
    const [dataMaryland, setDataMaryland] = useState("");
    const [dataDC, setDataDC] = useState("");

    useEffect(()=> {
        const getDataVirginia = async() => {
            const data = await fetch("https://disease.sh/v3/covid-19/states/virginia")
                .then((response) => response.json());
            setDataVirginia(data);
            setVA_cases(data.todayCases);
            setVA_deaths(data.todayDeaths);
            
        }

        const getDataMaryland = async() => {
            const data = await fetch("https://disease.sh/v3/covid-19/states/maryland")
                .then((response) => response.json());
            setDataMaryland(data);
            setMD_cases(data.todayCases);
            setMD_deaths(data.todayDeaths);
        }

        const getDataDC = async() => {
            const data = await fetch("https://disease.sh/v3/covid-19/states/District%20Of%20Columbia")
                .then((response) => response.json());
            setDataDC(data);
            setDC_cases(data.todayCases);
            setDC_deaths(data.todayDeaths);
        }

        getDataVirginia();
        getDataMaryland();
        getDataDC();
    }, [])

    const [DC_cases, setDC_cases] = useState(0);
    const [MD_cases, setMD_cases] = useState(0);
    const [VA_cases, setVA_cases] = useState(0);
    const [DC_deaths, setDC_deaths] = useState(0);
    const [MD_deaths, setMD_deaths] = useState(0);
    const [VA_deaths, setVA_deaths] = useState(0);
    
    const series = [{
      name: 'DC',
      data: [DC_cases]
    }, {
      name: 'Maryland',
      data: [MD_cases]
    }, {
      name: 'Virginia',
      data: [VA_cases]
    }];
    const series2 = [{
      name: 'DC',
      data: [DC_deaths]
    }, {
      name: 'Maryland',
      data: [MD_deaths]
    }, {
      name: 'Virginia',
      data: [VA_deaths]
    }];

    const options = {
        plotOptions: {
          bar: {
            horizontal: true,
            dataLabels: {
              position: 'top',
            },
          }
        },
        dataLabels: {
          enabled: false,
          offsetX: 20,
          style: {
            fontSize: '12px',
            colors: ['#fff']
          }
        },
        stroke: {
          show: true,
          width: 2,
          colors: ['#fff']
        },
        xaxis: {
          categories: ['Today'],
        },
        colors: ['#D33115', '#FCC400', '#303F9F'],
    }

    return (
        <div className="LocalDataMain">
            <img className="dmv" src={require("./images/dmv-graphic.png")} alt="dmv"/>
            <div className="data">
                <div>
                    <h1 style={{color: "#D33115"}}>{dataDC.state}</h1>
                    <p><b>Population:</b> {dataDC.population}</p>
                    <p><b>Active cases:</b> {dataDC.active}</p>
                </div>             
                <div>
                    <h1 style={{color: "#FCC400"}}>{dataMaryland.state}</h1>
                    <p><b>Population:</b> {dataMaryland.population}</p>
                    <p><b>Active cases:</b> {dataMaryland.active}</p>
                </div>
                <div>
                    <h1 style={{color: "#303F9F"}}>{dataVirginia.state}</h1>
                    <p><b>Population:</b> {dataVirginia.population}</p>
                    <p><b>Active cases:</b> {dataVirginia.active}</p>
                </div>
            </div>
            <h2 style={{color: "#2f4f4f"}}>Total Cases</h2>
            <div>
              <Chart
                options={options}
                series={series}
                type="bar"
                height={200}
              />
            <h2 style={{color: "#2f4f4f"}}>Total Deaths</h2>
              <Chart
                options={options}
                series={series2}
                type="bar"
                height={200}
              />
            </div>
        </div>       
    )
}

export default LocalData
